 getUsers(ev) {
        var _this = this;
        this.$refs.ruleForm2.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            this.logining = true;
            //NProgress.start();
            
			
					var url = host.host+'/cms/manager/login';
					var _this = this;
			    $.ajax(url, {
							xhrFields: {
			           withCredentials: true
			       	},
       				crossDomain: true,
			        data: {
			        	userName:_this.ruleForm2.account,
			        	pwd:_this.ruleForm2.checkPass 
